@use '../util' as *;

h1,
h2,
h3,
p {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: .1875rem;
  @include breakpoint(large) {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-color2);
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}
